<p>一，蘭花的最佳生長土壤：蘭花用土應以腐殖質為主，主要採用腐葉土或山林腐殖土。在南方用原產地的腐殖土，俗稱蘭花泥；也可用腐葉土、蛭石、珍珠岩等及人工配製成疏鬆、通氣、透水的培養土。所配製培養土均是呈微酸性的。選配不當，保水性太強或澆水過頻，導致土壤老是濕漉漉的。當這種過濕狀態持續時間超過蘭花所能的忍受限度時，蘭根就腐爛了，蘭株最終死亡。此外，也有人聽說蘭花「怕」水，就很少給它澆水，結果矯枉過正，蘭花被「渴死」了，這種情況也有的。由此可見，能否掌控好土壤中的含水量，是決定養蘭成敗的關鍵。</p>
