  methods: {
    go_page(route) {
      this.$router.push(route);
    },
    // fb login
    /* eslint-disable */
    fb_login() {
      const vm = this;
      FB.login(
          (response) => {
          vm.fb_status_change(response);
          },
          {
          scope: "email, public_profile",
          return_scopes: true,
          },
      );
      if (this.authorized) {
        const id = `fb__${this.profile.id}`;
        const member = this.members.find((item) => item.id === id);
        if (member === undefined) {
          // register
          const newMmeber = {
            id: `fb__${id}`,
            name: `${this.profile.name}`,
            gender: '',
            birthday: '',
            tel: '',
            email: `${this.profile.email}`,
            password: '',
            favorites: [],
          };
          this.register(newMember);
        } else {
          // login
          this.login(id);
        }
      }
    },
    fb_logout() {
      const vm = this;
      FB.logout((response) => {
          vm.fb_status_change(response);
      });
      if (!this.authorized) {
        this.logout();
      }
    },
    fb_status_change (response) {
    const vm = this;
    if (response.status === 'connected') {
        vm.authorized = true;
        vm.fb_getProfile();
    } else if (response.status === 'not_authorized') {
        vm.authorized = false;
        vm.profile = {};
    } else {
        vm.authorized = false;
        vm.profile = {};
    }
    },
    fb_getProfile() {
    FB.api('/me?fields=name,id,email', (response) => {
        this.profile = response;
    });
    },
    /* eslint-enable */
    login(id) {
      const thisMember = this.members.find((item) => item.id === id);
      this.member = thisMember;
      alert('登入成功');
    },
    logout() {
      this.member = {
        id: '',
        name: '',
        gender: '',
        birthday: '',
        tel: '',
        email: '',
        password: '',
        favorites: [],
      };
      if (this.authorized) {
        this.fb_logout();
      }
      alert('登出成功');
      this.go_page('');
    },
    register(newMember) {
      this.members.push(newMember);
      alert('註冊成功');
    },
  },
  created() {
    this.$bus.$on('son_call_data', () => {
      this.$bus.$emit('son_get_data', this.$data);
    });
    this.$bus.$on('add_new_mail', (mail) => {
      this.mails.push(mail);
    });
    this.$bus.$on('add_new_member', (member) => {
      this.register(member);
    });
    this.$bus.$on('log_in', (member) => {
      this.login(member.id);
    });
    this.$bus.$on('log_out', () => {
      this.logout();
    });
    this.$bus.$on('fb_login', (boolean) => {
      if (boolean) {
        this.fb_login();
        // profile: {email: '', id: '', name: ''},
      } else {
        this.fb_logout();
      }
    });
    this.$bus.$on('edit_favorite', (id, boolean) => {
      if (boolean) {
        this.member.favorites.push(id);
      } else {
        const idIndex = this.member.favorites.findIndex((item) => item === id);
        this.member.favorites.splice(idIndex, 1);
      }
    });
    this.$bus.$on('add_new_order', (newOrder) => {
      this.orders.push(newOrder);
    });
    this.$bus.$on('make_payment', (id, payment) => {
      console.log(id, payment);
      const paidOrder = this.orders.find((item) => item.id === id);
      paidOrder.payment = payment;
      paidOrder.payment.status = 'paid';
    });
  },
  mounted() {
    /* eslint-disable */
    const vm = this;
    window.fbAsyncInit = function () {
      FB.init({
      appId: "256052322609908",
      cookie: true,
      xfbml: true,
      version: "v9.0",
      });
      FB.AppEvents.logPageView();
      FB.getLoginStatus((response) => {
        vm.fb_status_change(response);
      });
    };
    /* eslint-enable */
  },
};
